.navbar {
  margin: 16px 0px;
}

.navbar__list {
  display: flex;
  list-style: none;
}

.navbar__link {
  text-decoration: none;

  font-size: 16px;
  line-height: 20px;

  color: #ccc;

  &.active {
    color: #3a80ba;
  }
}

.navarrow {
  color: #ccc;
  margin: 0 8px;
}
